<template>
	<div>
<!-- 	<swiper  :options="swiperOption" id="swiperBox" v-if="list.length">
	  <swiper-slide class="swiper-slide"  :key="item.id">
	  	内容1
	  </swiper-slide>
	  <swiper-slide class="swiper-slide"  :key="item.id">
	  	内容2
	  </swiper-slide>
	  <swiper-slide class="swiper-slide"  :key="item.id">
	  	内容3
	  </swiper-slide>
	  <div class="swiper-pagination"  slot="pagination"></div>
	</swiper> -->

	<div class="content-wrap">
			<div class="cart-tool">
				<div class="center-wrap">
					<span>共{{this.cartNum}}件商品</span>
					<span v-if="!isEmpty" @click="status=!status">{{this.status?'管理':'完成'}}</span>
				</div>
			</div>
			<div class="noDataTips" v-if="isEmpty">购物车是空的</div>
			<ul class="cart-list" v-else>
				<div class="center-wrap">
				<li v-for="(item,index) of cartList">
					<span class="check-box-wrap">
					<input type="checkbox"  :value="item.cid" v-model="checkList" :id="'g_'+index"/>
					<label  class="ui-checkbox" :for="'g_'+index"></label>
					</span>
					<router-link :to="item.url" class="img-wrap" tag="div">
						<img :src="item.spic" >	
					</router-link>
					<div>
						<router-link tag="p" :to="item.url" class="ellipsis2">{{item.goodName}}</router-link>
						<p class="justify">
							<span class="type">{{item.type}}</span>  
						</p> 
						<p class="info-footer">
							<span class="theme-color">{{item.price|aprice}}</span>
							<span>
								<span class="reduce-btn" @click="desBtn(index)">-</span>
								<input type="number" oninput="this.value=this.value.replace(/(^0|[^\d])/g,'')"  @change="reg($event,index)" v-model.number="item.num" class="input-area">
								<span class="increase-btn" @click="addBtn(index)">+</span>
							</span>
						</p>
					</div>
					<!-- <span @click="itemDelete(index)" v-show="!status" class="icon-delete"></span> -->
				</li>
				</div>
			</ul>
		
	</div>
	<div class="cart-footer-nav" v-if="!isEmpty">
		<div>
		<input type="checkbox" id="all" :checked="cartList.length==checkList.length&&cartList.length" @click="check_All">
		<label for="all" class="ui-checkbox"></label>
		全选
		</div>
		<div>
		<template v-if="status">
		合计
		<span class="theme-color">{{totalPrice|aprice}}</span>
		<span @click="pay">去结算</span>
		</template>
		<span v-else @click="delSelect">删除所选</span>
		</div>
	</div>
	<myfooter></myfooter>
	</div>
</template>
<script>

	import myfooter from "common/footer";
	import {mapState,mapGetters} from "vuex";
	import qs from 'qs'
	// import axios from "axios";
	import { Dialog } from 'vant';
	import tipShow from "@/assets/js/tips.js"
	// import 'vant/lib/dialog/style';
	// import 'vant/lib/toast/style';
	export default {
		data (){
			return {
				checkList:[],
				// true为购物车的正常状态
				// false为编辑状态
				status:true
			}
		},
		filters:{
			aprice(value){
				return '￥'+value;
			}
		},
		components:{myfooter},
		computed:{
			...mapState(['cartList','orderList']),
			...mapGetters(['cartNum']),
			isEmpty:function() {
				return this.cartNum==0;
			},
			isNoChoose:function() {
				return this.checkList.length==0;
			},
			totalPrice:function() {
				let price=0;
				const chooseProducts = this.cartList.filter(el => this.checkList.indexOf(el.cid) > -1);
				 chooseProducts.forEach((item)=>{
					price+=Number(item.price)*Number(item.num)
				},0)
				 return price;
			}
		},
		methods:{
			desBtn(index){
				// this.cartList[index].num--;
				var val=this.cartList[index].num;
				if(val<=1){
					return ;
				}else{
					val--;
				}
				this.$store.commit("updateGoods",{"index":index,"key":"num",value:val})
			},
			addBtn(index){
				// this.cartList[index].num++;
				var val=this.cartList[index].num;
				val++;
				this.$store.commit("updateGoods",{"index":index,"key":"num",value:val})
			},
			reg(e,index){
				
				var val=this.cartList[index].num;
				val=val==""?1:val
				// if(!/^[1-9]\d*$/.test(val)){
				// 	// console.log(val);
				// 	val=val.replace(/\D+/g,"");
				// 	// console.log(val);
				// 	tipShow("不正确的格式将被转换!");
				// }
				this.$store.commit("updateGoods",{"index":index,"key":"num",value:val})
			},
			findPosition(id){
			      return this.goodsList.findIndex(item=>{
			          return item.id==id
			       })
			 },
			 del(id){
			      var i=this.findPosition(id);
			      
			 },
			itemDelete(index){
				Dialog.confirm({
				    title: '删除商品',
				    message: '确认将该商品从购物车移除',
				}).then(() => {

					var cid=this.cartList[index].cid;
					this.checkList=this.checkList.filter(function(ele) {
						return ele!=cid;
					})
					//这个商品就从选项中移除了
					//

					// this.cartList.splice(index,1)
					this.$store.commit("deleteGoods",index);
				    tipShow('删除成功');
				}).catch(err=>{

				})
			},
			delSelect(){

				Dialog.confirm({
				    title: '删除商品',
				    message: '是否将选中的商品从购物车移除',
				}).then(() => {
					this.checkList.map((ele)=> {
						this.cartList.forEach((cartele,index)=> {
							if(cartele.cid==ele){
								// this.cartList.splice(index,1)
								this.$store.commit("deleteGoods",index);
							}
						})
					})
					this.checkList=[]
					
				    tipShow('删除成功');
				}).catch(err=>{
				})

			},
			check_All(){
				if(this.checkList.length==this.cartList.length){
					this.checkList=[]
				}else{
					this.checkList=this.cartList.map(ele=>{
						return ele.cid;
					})
				}
			},
			pay(){
				if(this.isNoChoose){
					tipShow("当前没有选择任何商品")
				}else{
					var chooseList=this.cartList.filter((ele)=> {
						if(this.checkList.includes(ele.cid)){
							return ele
						}
					})
					this.$store.commit("addOrder",chooseList)
					this.$router.push({path:"/Order",query:{from:"/Cart"}});
				}
			}
		},
		mounted:function() {
			this.bus.$on("clearCheck",()=>{
				this.checkList=[]
			})
		}
	}
</script>
<style>

</style>
<style scoped>

	.content-wrap{
		overflow: auto;
		-webkit-overflow-scrolling: touch;
		position: fixed;
		top: 0px;
		width: 100%;
		bottom: 98px;
	}
	.center-wrap{
		width: 94%;
		margin: 0 auto;
	}
	.cart-list{
		position: relative;
		overflow: hidden;
	}
	.cart-list:before{
		width:100%;
		height: 20vh;
		background-image: -webkit-linear-gradient(left, #e24648, #e6292c);
		background-image: -o-linear-gradient(left, #e24648, #e6292c);
		background-image: linear-gradient(to right, #e24648, #e6292c);
		content:"";
		position: absolute;
		z-index: -1;
	}
	.cart-tool {
		color: #fff;
		line-height: 50px;
		background-image: -webkit-linear-gradient(left, #e24648, #e6292c);
		background-image: -o-linear-gradient(left, #e24648, #e6292c);
		background-image: linear-gradient(to right, #e24648, #e6292c);
	}
	.cart-tool .center-wrap{
		display: flex;
		justify-content: space-between;
	}
	.cart-list li{
		display: flex;
		padding:15px 10px;
		margin: 10px 0px;
		border-radius: 10px;
		background: #fff;
		align-items: center;
	}

	.info-footer{
		display: flex;
		justify-content:space-between;
		align-items: center;
	}
	.theme-color{
		color: #e6292c;
	}
	.icon-delete{
		width: 40px;
		flex-shrink: 0;
		height: 30px;
		background: url(../../assets/img/delete.svg) center center no-repeat;
		background-size: 26px;
		display: inline-block;
	}
	.input-area{
		text-align: center;
		width: 40px;
		border: 1px solid #eee;
		float: left;
		height: 28px;
		line-height: 28px;
	}
	.reduce-btn{
		border:1px solid #eee;
		border-right: 0px;
	}
	.increase-btn{
		border:1px solid #eee;
		border-left: 0px;
	}
	.reduce-btn,.increase-btn{
		width: 28px;
		height: 28px;
		float: left;
		text-align: center;
		line-height: 28px;
	}
	.img-wrap{
		width: 20vw;
		height: 20vw;
		flex-shrink: 0;
		margin: 0px 10px;
	}
	.img-wrap img{
		width: 100%;
		height: 100%;
	}
	.type{
		color: #999;
		background: #eee;
		border-radius: 4px;
		display: inline-block;
		padding: 2px 5px;
		margin-top: 4px;
	}

	input[type=checkbox] {
		position: absolute;
		opacity: 0;
		width: 20px;
		height: 20px;
		filter: alpha(opacity=0);
		cursor: pointer;
		z-index: -1;
	}

	
	.ui-checkbox{
		width: 18px;
		height: 18px;
		border: 1px solid #e6292c;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		position: relative;
		display: inline-block;
		background: #fff;
	}

	:checked+.ui-checkbox:after{
		content: "";
		background: #e6292c;
		width: 10px;
		height: 10px;
		position: absolute;
		left: 0px;
		right: 0px;
		bottom: 0px;
		top: 0px;
		margin: auto;
		border-radius: 50%;
	}
	.cart-footer-nav{
		position: fixed;
		bottom: 46px;
		left: 0;
		width: 100%;
		height: 48px;
		line-height: 48px;
		display: flex;
		padding-left:20px;
		box-sizing: border-box;
		border-top: 1px solid #eee;
		background: #fff;
		justify-content: space-between;
		align-items: center;
	}
	.cart-footer-nav span:last-child{
		color:#fff;
		background: #e6292c;
		width: 100px;
		text-align: center;
		height: 100%;
		display: inline-block;
		margin-left: 5px;
	}
</style>


